	opacity: 0;
	z-index: 1;
	transition: opacity 0.25s;
	pointer-events: none;
}

.tooltip::after {
	content: " ";
	position: absolute;
	top: 100%; /* At the bottom of the tooltip */
	left: 50%;
	margin-left: -15px;
	border-width: 10px;
	border-style: solid;
	border-color: black transparent transparent transparent;
}

