.shade {
	width: 100%;
	height: 100%;
	position: absolute;
	background-color: rgba(28, 28, 28, 0.68);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 2;
	pointer-events: all;
}

.container {
	width: 38.1%;
	height: 38.1%;
	position: absolute;
	padding: 5px;
	z-index: 2;
	background-color: rgb(36, 36, 36);
	display: flex;
	align-items: stretch;
}

.alertBox {
	background-color: rgb(45, 45, 45);
	padding: 10px;
	display: grid;
	grid-template-rows: auto 1fr 25px;
	grid-template-areas:
		'title title'
		'textBox textBox'
		'no yes';
	z-index: 2;
	border-radius: 15px;
	overflow: hidden;
	width: 100%;
}

.title {
	grid-area: title;
	display: flex;
	justify-content: center;
	background-color: #1e1e1e;
	box-sizing: border-box;
	padding: 5px;
	border-radius: 15px;
}

.error {
	color: red;
}

.textBox {
	grid-area: textBox;
	margin: 5px;
	padding: 5px;
	display: block;
	text-align: left;
	overflow: scroll;
	font-weight: 300;
	box-sizing: border-box;
	width: unset;
}

.textBox ul {
	padding-inline-start: 20px;
}

.response {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
}

button.NO {
	grid-area: no;
	background-color: darkred
}

button.OK {
	grid-column-start: no;
	grid-column-end: yes;
}

button.ERROR {
	background-color: darkred;
}

button.YES {
	grid-area: yes;
	background-color: darkgreen;
}

button.NO, button.YES, button.OK {
	border: none;
    color: white;
	border-radius: 15px;
}

.loading {
	font-size: 5vw;
}

.loading > svg {
	animation: rotate 1s linear infinite;
}
@keyframes rotate {
    0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(180deg)
    }
    100% {
        transform: rotate(360deg)
    }
}